import uCharts from '@/js_sdk/u-charts/u-charts/u-charts.js';
var _self;
	var canvaLineA=null;
export default{
	data(){
		return{
			active:1,
			active2:1
		}
	},
	mounted(){
		_self = this;
		var info=uni.getSystemInfoSync()
		console.log(info)
					// uni.upx2px(750) 这是uni-app自带的自适应，以750的尺寸为基准。动态变化
					this.cWidth=info.windowWidth;
					this.getServerData();
	},
	onLoad(){
		
	},
	onShow(){
	},
	methods:{
		toggle(t){
			this.active=t
		},
		toggle2(t){
			this.active2=t
		},
		getServerData(){
			let LineA={categories:[],series:[]};
								LineA.categories=['2010','2012','2014','2016','2018','2020'];
								LineA.series=[{
									name:"南京",
									data:[12,15,10,18,6,13],
									color:'#409eff'
								},{
									name:"苏州",
									data:[15,6,13,18,8,14],
									color:'#e6a23c'
								},{
									name:"无锡",
									data:[13,12,13,16,9,10],
									color:'#f56c6c'
								}];
								_self.showLineA("canvasLineA",LineA);
					},
					// 展示图标的函数 接收参数，一个块的id,一个数据
					showLineA(canvasId,chartData){
						canvaLineA=new uCharts({
											$this:_self,
											canvasId: canvasId,
											type: 'line',
											fontSize:11,
											legend:{show:false,position:'top',float:'left'},
											dataLabel:false,
											dataPointShape:true,
											background:'#FFFFFF',
											pixelRatio:_self.pixelRatio,
											categories: chartData.categories,
											series: chartData.series,
											animation: true,
											xAxis: {
												type:'grid',
												gridColor:'#CCCCCC',
												gridType:'dash',
												dashLength:8
											},
											yAxis: {
												gridType:'dash',
												gridColor:'#CCCCCC',
												dashLength:8,
												splitNumber:5,
												min:10,
												max:180,
												format:(val)=>{return val.toFixed(0)+'元'}
											},
											width: _self.cWidth,
											height: 200,
											extra: {
												line:{
													type: 'straight'
												}
											}
										});
					},
					// 点击图表显示的内容
					touchLineA(e) {
						// 使用声明的变量canvaLineA
						console.log(canvaLineA)
						canvaLineA.showToolTip(e, {
							format: function (item, category) {
								return category + ' ' + item.name + ':' + item.data 
							}
						});
					}
	}
}
